:root {
  font-size: 16px;
}

.paymentSuccessfulContainer {
 padding-bottom: 2rem;
}

/* ************* */
.sectionContainer {
  padding: 0.8rem 0rem;
}

/* ************* */
.paymentSuccessfulDisplay {
  margin: 1.2rem 0rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  gap: 0.3rem;
  color: green;

  border-radius: 8px;
  border :  1px solid green;
}
.headingMessage {
  color: green;
  font-size: 1.8rem;
  font-weight: bold;
}

.billingMessage {
  color: green;
  font-size: 1rem;
  font-weight: bold;
}

.title {
  border: 1px solid black;
  padding: 0.7rem 10px !important;
  border-radius: 06px;
  text-transform: uppercase;
  font-size: 1.1rem;
  margin-bottom: 1.2rem;
}



/* **************** */
.sectionContainer > div{
    font-weight: 700;
    padding: 0.2rem 0.3rem;
}
.sectionContainer > div span{
    font-weight: 500;
    border-bottom: 1px dotted rgb(0, 0, 0);
    border-radius : 8px;
    padding: 0rem 0.5rem;
}



@media screen and (max-width : 991px){
    .sectionContainer > div span{
        display: block;
    }
}